.main-navigation {
	a {
		font-weight: 100;
	}
	p {
		font-weight: 100;
	}
}

.main-navigation {
	@include desktop {
		justify-content: flex-end;
	}
	p {
		margin-top: 0;
	}
	.menu {
		.nav-menu {
			display: flex;
			padding: 0;
			margin: 0;
			list-style: none;
			li {
				display: flex;
				a {
					display: flex;
				}
			}
		}
	}
	button {
		display: none;
	}
}
